import history from '@/utils/history'
import store from '@/store'
import hotkeys from 'hotkeys-js'
import { clearProject, createLayer } from '@/utils/shared'

export default function(app) {
  hotkeys('b', () => store.commit('setTool', 'brush')) // 
  hotkeys('p', () => store.commit('setTool', 'pencil')) // 
  hotkeys('e', () => store.commit('setTool', 'eraser')) // 
  hotkeys('a', () => store.commit('setTool', 'select')) // 
  hotkeys('ctrl+z', () => history.undo()) // 
  hotkeys('ctrl+shift+z', () => history.redo()) // 
  hotkeys('ctrl+q', () => {
    clearProject()
    createLayer()
  })
}
