/**
 * 
 * @param {Object} param 
 *  https://ai.baidu.com/docs#/TTS-API/41ac79a6
 * @param {Object} options api
 *           timeout {number}  60
 *           volume {number} audio 0-1
 *           hidden {boolean} audio
 *           autoDestory {boolean} 
 *           onInit {Function} audio
 *           onSuccess {Function} 
 *           onError {Function}  
 *           onTimeout {Function} 60
 */
export default function btts(param, options) {
  var url = 'https://tsn.baidu.com/text2audio'
  var opt = options || {}
  var p = param || {}

  // autoplay
  var audio = document.createElement('audio')
  if (opt.autoplay) {
    audio.setAttribute('autoplay', 'autoplay')
  }

  // 
  if (!opt.hidden) {
    audio.setAttribute('controls', 'controls')
  } else {
    audio.style.display = 'none'
  }

  // 
  if (typeof opt.volume !== 'undefined') {
    audio.volume = opt.volume
  }

  // onInit
  isFunction(opt.onInit) && opt.onInit(audio)

  // 60
  var DEFAULT_TIMEOUT = 60000
  var timeout = opt.timeout || DEFAULT_TIMEOUT

  // XMLHttpRequest
  var xhr = new XMLHttpRequest()
  xhr.open('POST', url)

  // form
  var data = {}
  for (var p in param) {
    data[p] = param[p]
  }

  // 
  data.cuid = data.cuid || data.tok
  data.ctp = 1
  data.lan = data.lan || 'zh'
  data.aue = data.aue || 3

  // 
  var fd = []
  for (var k in data) {
    fd.push(k + '=' + encodeURIComponent(data[k]))
  }

  // blob
  var frd = new FileReader()
  xhr.responseType = 'blob'
  xhr.send(fd.join('&'))

  // timeout
  var timer = setTimeout(function() {
    xhr.abort()
    isFunction(opt.onTimeout) && opt.onTimeout()
  }, timeout)

  xhr.onreadystatechange = function() {
    if (xhr.readyState == 4) {
      clearTimeout(timer)
      if (xhr.status == 200) {
        if (xhr.response.type === 'audio/mp3') {
          // bodyapppend
          document.body.appendChild(audio)

          audio.setAttribute('src', URL.createObjectURL(xhr.response))

          // autoDestoryaudiodom
          if (opt.autoDestory) {
            audio.onended = function() {
              document.body.removeChild(audio)
            }
          }

          isFunction(opt.onSuccess) && opt.onSuccess(audio)
        }

        // 
        if (xhr.response.type === 'application/json') {
          frd.onload = function() {
            var text = frd.result
            isFunction(opt.onError) && opt.onError(text)
          }
          frd.readAsText(xhr.response)
        }
      }
    }
  }

  // 
  function isFunction(obj) {
    if (Object.prototype.toString.call(obj) === '[object Function]') {
      return true
    }
    return false
  }
}
